import * as React from 'react';
import { IPlayer } from '../state/state-interfaces';
import { BaseComponent } from './BaseComponent';
import './PlayerResources.css';

export class PlayerResources extends BaseComponent<{player:string}> {
  constructor(props:{player:string}){
    super(props)
  }
  public render() {
    const player = this.state.players.find(p=>p.id===this.props.player)
    return (
      <div className="PlayerResources">
        {player.name}
        <ul className="resource-list">
          {player.resources.map(r=>
          <li key={r.id}><img src={r.icon} alt={r.name}/>:{r.value}</li>
          )}
        </ul>
      </div>
    );
  }
}



